.site-list {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  list-style: none;
}

.site-list__item {
  margin-left: -20px;
  margin-right: -20px;
  padding-left: 20px;
  padding-right: 20px;
  border-bottom: 1px solid #d9d9d9;

  text-align: center;
}

.site-list__link {
  display: inline-block;

  padding-top: 22px;
  padding-bottom: 23px;

  font-size: inherit;
  font-weight: 500;
  color: #111111;
}

.site-list__link:hover {
  opacity: 0.6;
}

.site-list__link:active {
  opacity: 0.3;
}

@media (min-width: $tablet-width) {
  .site-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-right: -17px;
  }

  .site-list__item {
    position: relative;
    margin: 0;
    padding: 0;
    border: none;
    font-size: 18px;
    line-height: 24px;
  }

  .site-list__item--active .site-list__link::after {
    content: "";
    position: absolute;
    left: 21px;
    top: 42px;
    width: calc(100% - 38px);
    height: 2px;
    background-color: #68b738;
  }

  .site-list__link {
    padding: 10px 14px;
    text-align: left;
    font-weight: 400;
    line-height: 25px;
    color: #222222;
  }
}

@media (min-width: $desktop-width) {
  .site-list__link {
    padding: 9px 13px;
    font-size: 20px;
    color: #111111;
  }

  .main-nav__list--index .site-list__link--color {
    color: #ffffff;
  }

  .main-nav__list--index .site-list__link::after {
    background-color: #ffffff;
  }
}
